import { Polygon } from "react-leaflet";
import { forwardRef, useEffect } from "react";

export const Selection = forwardRef((props, ref) => {
  useEffect(() => {
    ref?.current?.enableEdit();
  }, [ref]);

  return (
    <Polygon
      ref={ref}
      eventHandlers={{}}
      editable
      fill={false}
      positions={[
        [47.831365, 16.430752],
        [47.825229, 16.412673],
        [47.83874, 16.398811],
        [47.845135, 16.405416],
        [47.831365, 16.430752],
      ]}
    />
  );
});
