// https://overpass-turbo.eu/
import overpassResult from "./overpass.json";
import overpassCityResult from "./overpass-city.json";
import { Polygon } from "geojson";
import { OverpassJsonOutput } from "overpass-ql-ts";

const fetchApi = async (query: string) => {
  const url = `https://overpass-api.de/api/interpreter?data=${encodeURIComponent(query)}`;
  try {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`failed to fetch overpass: ${await response.text()}`);
    }
    return response.json();
  } catch (error) {
    alert(error.message);
    console.error(error);
    throw error;
  }
};

export const fetchRoads = async (
  polygon: Polygon,
): OverpassJsonOutput["elements"] => {
  // TODO: Remove for production
  // return overpassResult;

  const coords = polygon.coordinates
    .flatMap((v) => v.flatMap((v) => [v[1], v[0]]))
    .join(" ");

  const query = `
[out:json];
way(poly:"${coords}")[highway][name];
out geom;`;

  return fetchApi(query);
};

export const fetchCityBoundaries = async (
  osmType: string,
  osmId: number,
): OverpassJsonOutput["elements"] => {
  // TODO: Remove for production
  // return overpassCityResult;

  const query = `
[out:json];
${osmType}(${osmId});
out geom;`;

  return fetchApi(query);
};
