import "leaflet-editable";
import { FeatureGroup, MapContainer, Pane, TileLayer } from "react-leaflet";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { Roads } from "./roads";
import { fetchCityBoundaries, fetchRoads } from "../overpass";
import { Selection } from "./selection";
import { LatLng, LatLngExpression } from "leaflet";
import { LeafletControlGeocoder } from "./geocoder";
import simplify from "simplify-geojson";
import osm2geojson from "osm2geojson-lite";
import { Form } from "./form";

export type Road = {
  name: string;
  positions: LatLngExpression[][];
  state: "correct" | "wrong" | "pending" | "current";
};

export type GameState = {
  roads: Road[];
};

const random = (max: number) => Math.floor(Math.random() * max);

export const Map = () => {
  const selectionRef = useRef(null);
  useEffect(() => selectionRef.current?.enableEdit(), [selectionRef]);

  const [gameState, setGameState] = useState<GameState>({
    roads: [],
  });

  const startGame = useCallback(async () => {
    const rawRoads = (
      await fetchRoads(selectionRef.current.toGeoJSON().geometry)
    ).elements;
    const roads = Object.values(
      rawRoads.reduce((roads, road) => {
        const r: Road = {
          name: road.tags.name,
          state: "pending",
          positions: [],
        };

        roads[r.name] ??= r;
        roads[r.name].positions.push([
          road.geometry.map((point) => new LatLng(point.lat, point.lon)),
        ]);

        return roads;
      }, {}),
    );

    const shuffledRoads = roads
      .map((value) => ({ value, sort: Math.random() }))
      .sort((a, b) => a.sort - b.sort)
      .map(({ value }) => value);

    shuffledRoads[random(shuffledRoads.length - 1)].state = "current";

    selectionRef.current?.disableEdit();
    selectionRef.current?.setStyle({ opacity: 0 });

    setGameState({ roads: shuffledRoads });
  }, [selectionRef]);

  const cancelGame = useCallback(() => {
    setGameState({ roads: [] });
    selectionRef.current?.enableEdit();
    selectionRef.current?.setStyle({ opacity: 1 });
  }, [setGameState]);

  const submitGuess = useCallback(
    (value, cb: (result: boolean) => void) => {
      const guess = value
        .trim()
        .toLowerCase()
        .replaceAll("ä", "a")
        .replaceAll("ö", "o")
        .replaceAll("ü", "u")
        .replaceAll("ß", "ss");

      setGameState((state) => {
        const currentRoad = state.roads.find(
          (road) => road.state === "current",
        )!;
        const roadName = currentRoad.name
          .trim()
          .toLowerCase()
          .replaceAll("ä", "a")
          .replaceAll("ö", "o")
          .replaceAll("ü", "u")
          .replaceAll("ß", "ss");

        const result = guess === roadName;
        currentRoad.state = result ? "correct" : "wrong";

        // Determine the next road
        const pendingRoads = state.roads.filter(
          (road) => road.state !== "correct",
        );
        pendingRoads[random(pendingRoads.length - 1)].state = "current";

        cb(result);
        return { ...state };
      });
    },
    [setGameState],
  );

  const onSearch = useCallback(
    async (osmType: string, osmId: number) => {
      const boundaries = await fetchCityBoundaries(osmType, osmId);

      const geo = { type: "Feature", geometry: osm2geojson(boundaries) };
      const positions = [];

      if (geo.geometry.type === "FeatureCollection") {
        const [lat, lon] = geo.geometry.features[0].geometry.coordinates;
        const lonOffset = 0.007;
        const latOffset = 0.01;
        const bbox = [
          [lon - lonOffset, lat + latOffset],
          [lon + lonOffset, lat + latOffset],
          [lon + lonOffset, lat - latOffset],
          [lon - lonOffset, lat - latOffset],
        ];
        positions.push(...bbox);
      } else {
        const simplified = simplify(geo, 0.001);
        positions.push(
          ...simplified.geometry.coordinates[0].map(([lon, lat]) => [lat, lon]),
        );
      }
      selectionRef.current?.disableEdit();
      selectionRef.current?.setLatLngs(positions);
      selectionRef.current?.enableEdit();
      selectionRef.current?.redraw();
    },
    [selectionRef],
  );

  const roads = useMemo(
    () => ({
      correct: gameState.roads.filter((road) => road.state === "correct"),
      wrong: gameState.roads.filter((road) => road.state === "wrong"),
      current: gameState.roads.filter((road) => road.state === "current"),
    }),
    [gameState],
  );

  const selection = useMemo(() => <Selection ref={selectionRef} />, []);

  return (
    <>
      <Form
        gameState={gameState}
        startGame={startGame}
        cancelGame={cancelGame}
        submitGuess={submitGuess}
      />

      <MapContainer
        style={{
          height: "100%",
          width: "100%",
          left: 0,
          top: 0,
          position: "absolute",
        }}
        center={[47.8305, 16.4]}
        zoom={14}
        scrollWheelZoom
        editable
      >
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
          url={`https://{s}.basemaps.cartocdn.com/dark_nolabels/{z}/{x}/{y}{r}.png`}
          subdomains="abcd"
          maxZoom={20}
          edgeBufferTiles={5}
          opacity={gameState.roads.length ? 1 : 0}
        />
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
          url={`https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png`}
          subdomains="abcd"
          maxZoom={20}
          edgeBufferTiles={5}
          opacity={gameState.roads.length ? 0 : 1}
        />
        <LeafletControlGeocoder onSearch={onSearch} />
        <Pane
          name="selection"
          style={{ opacity: gameState.roads.length ? 0 : 1 }}
        >
          {selection}
        </Pane>
        <Pane name="roads" style={{ opacity: 0.5 }}>
          <Roads color="#00F" currentRoad roads={roads.current} />
          <Roads color="#0F0" roads={roads.correct} />
          <Roads color="#F00" roads={roads.wrong} />
        </Pane>
      </MapContainer>
    </>
  );
};
