import {
  FeatureGroup,
  LayerGroup,
  Polyline,
  Tooltip,
  useMap,
} from "react-leaflet";
import { Road } from "./map";
import { useEffect, useRef } from "react";

export const Roads = (props: {
  color: string;
  currentRoad: boolean;
  roads: Road[];
}) => {
  const ref = useRef();
  const map = useMap();

  useEffect(() => {
    const feature = ref.current;
    if (props.currentRoad && feature) {
      map.setView(feature.getBounds().getCenter(), map.getZoom(), {
        animate: true,
      });
    }
  }, [props.roads]);

  return (
    <LayerGroup>
      {props.roads.map((road) => (
        <FeatureGroup ref={ref}>
          {road.positions.map((position) => (
            <Polyline
              weight={2.5}
              smoothFactor={1}
              color={props.color}
              positions={position}
            ></Polyline>
          ))}
          {!props.currentRoad ? <Tooltip>{road.name}</Tooltip> : null}
        </FeatureGroup>
      ))}
    </LayerGroup>
  );
};
