import { useCallback, useState } from "react";
import { GameState } from "./map";
import { CancelIcon } from "../icons/cancel";
import "./form.css";

export const Form = (props: {
  gameState: GameState;
  startGame: () => void;
  cancelGame: () => void;
  submitGuess: (value: string, cb: (result: boolean) => void) => void;
}) => {
  const [shake, setShake] = useState(false);

  const submitGuess = useCallback(
    (event) => {
      if (event.keyCode === 13) {
        props.submitGuess(event.target.value, resultCallback);
        event.target.value = "";
      }
    },
    [props.submitGuess],
  );

  const resultCallback = useCallback(
    (result: boolean) => {
      if (!result) {
        setShake(true);
        setTimeout(() => setShake(false), 1000);
      }
    },
    [setShake],
  );

  return (
    <div
      style={{
        zIndex: 1000000,
        position: "absolute",
        bottom: "50px",
        right: "50px",
        left: "50px",
      }}
    >
      {props.gameState.roads.length ? (
        <div className="container" style={{ display: "flex", gap: "8px" }}>
          <input
            type="text"
            placeholder="Enter street name"
            onKeyDown={submitGuess}
            autoFocus
            className={`input text-input ${shake ? "shake" : ""}`}
          />
          <button className="button" onClick={props.cancelGame}>
            <span className="icon is-small">
              <CancelIcon color="#fff" />
            </span>
          </button>
        </div>
      ) : (
        <button className="button is-fullwidth" onClick={props.startGame}>
          Start
        </button>
      )}
    </div>
  );
};
